import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class RestrictionService {

	private maxArticles : number;

  constructor() { }

  public setMaxArticles(max : number){
  	this.maxArticles = max;
  }

  public getMaxArticles() : number{
  	return this.maxArticles;
  }
}
