import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
  selector: '[appUsyncInput]'
})
export class UsyncInputDirective {
  constructor(private el: ElementRef) {
  }
//oninput

 @HostListener("input", ["$event.target.value"])
  onInput(value) {
  	var elemento = this.el.nativeElement;
  	if (elemento.hasAttribute('required')) {
  		var parent = elemento.parentNode;
  		var message = this.getNodeAttribute('message', parent.childNodes);
      if (message != null) {
      if (value.length <= 0) {
        elemento.classList.add("error-input");
          message.innerHTML = "Campo Requerido.!";
        }else{
          elemento.classList.remove("error-input");
          message.innerHTML = "";
        }
      }
  	}else{
  		console.log('norequired '+value)
  	}
  }

  private getNodeAttribute(attr : string, list :Array<any>) : Element{  	
  	for (var i = 0; i < list.length; ++i) {
  		if (list[i].hasAttribute(attr)) {
			return list[i];
  		}
  	}
  	return null;
  }
}
