import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore'
import { ReplaySubject, Observable } from 'rxjs'
import { AngularFireStorage } from 'angularfire2/storage';
import { UserInfoService } from './../user-info/user-info.service';

@Injectable({
	providedIn: 'root'
})
export class UsuariosService {

	private usuarios = new ReplaySubject(1);
	private cantidad = new ReplaySubject(1);
	private cantidadUns: number = 0;
	constructor(private db: AngularFirestore,
		private user: UserInfoService) {
		var vm = this;
		var unidad = this.user.getUnidad();
		this.db.collection('/registros', ref => ref.where('unidad', '==', unidad)).snapshotChanges().subscribe(rsp => {
			var lista = [];
			if (rsp.length > 0) {
				rsp.forEach(function(doc, index, array) {
					var data = doc.payload.doc.data();
					var obj = {
						"obj": doc.payload.doc.data(),
						"id": doc.payload.doc.id
					};
					lista.push(obj);
					if ((index + 1) >= array.length) {
						console.log("articulos: " + array.length);
						vm.cantidadUns = array.length;
						vm.cantidad.next(array.length);
						vm.usuarios.next(lista);
					}
				});
			} else {
				vm.cantidad.next(0);
				vm.usuarios.next(lista);
			}
		});
	}

	public getUsuarios(): ReplaySubject<any> {

		return this.usuarios;
	}

	public agregarUsuario(obj, unidad): Promise<object> {
		//var url = "/registros/unidad/"+unidad;
		obj['unidad'] = this.user.getUnidad();
		return this.db.collection('/registros').add(obj);
	}

	public eliminarUsuario(obj): Promise<any> {
		var url = "/registros/" + obj.id;
		var vm = this;
		return this.db.doc(url).delete();
	}

	public getSizeUns(): number {
		return this.cantidadUns;
	}

	public actualizarUsuario(obj): Promise<any> {
		var url = "/registros/" + obj.id;
		var vm = this;
		return this.db.doc(url).update(obj.obj);
	}
}
