import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { InventarioService } from './../../../../provider/inventario/inventario.service';
import { UserInfoService } from './../../../../provider/user-info/user-info.service';
import { AngularFirestore } from 'angularfire2/firestore';
import { PInventarioModyComponent } from './../p-inventario-mody/p-inventario-mody.component';
import { ToasterModule, ToasterService, ToasterContainerComponent } from 'angular2-toaster';
@Component({
	selector: 'app-p-inventario',
	templateUrl: './p-inventario.component.html',
	styleUrls: ['./p-inventario.component.css']
})
export class PInventarioComponent implements OnInit {
	private articulos: Array<any> = [];
	public displayedColumns: string[] = ['imagen', 'codigoBarra', 'nombre', 'stock', 'ingreso', 'modificacion', 'acciones'];
	public dataSource: Array<any> = [];
	public selectedItem: any;
	public isOnEdit: boolean = false;
	public isCargando : boolean = false;
	@ViewChild(PInventarioModyComponent) mody: PInventarioModyComponent

	constructor(private inventario: InventarioService,
		private user: UserInfoService,
		private db: AngularFirestore,
		private toaster: ToasterService) {

		this.inventario.getInventario().subscribe(rsp => {
			this.dataSource = [];
			this.dataSource = rsp;
		});
	}

	ngOnInit() {
	}

	public dateParse(date): string {
		var rsp = "";
		if (date) {
			var dd = new Date(date);
			var split = dd.toJSON().split('T');
			split = split[0].split('-');
			rsp = split[2] + '-' + split[1] + '-' + split[0];
		}
		return rsp;
	}

	public deleteArticle(obj) {
		console.log(obj);
		this.isCargando = true;
		this.inventario.eliminarArticulo(obj).subscribe(rsp=>{
			this.isCargando = false;
		})
	}

	public onEdit(item) {
		this.selectedItem = Object.assign({}, item);
		this.isOnEdit = true;
	}

	public onClose(event : Event){
		this.isOnEdit = false;
		//@ts-ignore
		if (event.detail.mody) {
			//@ts-ignore
			var articulo = event.detail.object;
			this.inventario.actualizarArticulo(articulo);
			var mensaje = "Articulo "+articulo.obj.codigoBarra+" "+articulo.obj.nombre+" modificado correctamente.";
			this.toaster.pop('success', 'Modificacion Correcta.', mensaje);
		}else{

		}
	}

}