import { Component, OnInit, Input, OnDestroy, ChangeDetectorRef, ViewChild, ElementRef } from '@angular/core';
import { UserInfoService } from './../../../provider/user-info/user-info.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router, NavigationEnd } from '@angular/router';
import { MediaMatcher } from '@angular/cdk/layout';
import { MatSidenav } from '@angular/material';


@Component({
  selector: 'app-p-nav',
  templateUrl: './p-nav.component.html',
  styleUrls: ['./p-nav.component.css']
})
export class PNavComponent implements OnInit, OnDestroy {

  public navList: Array<any> = [];
  public opened: boolean = true;
  public imagen: string;
  public nombre: string;
  public dashboard: boolean = false;
  @Input() page: string;
  public mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  @ViewChild('sidenav') nav : MatSidenav;

  constructor(private user: UserInfoService,
    private afAuth: AngularFireAuth,
    private router: Router,
    private changeDetectorRef: ChangeDetectorRef,
    private media: MediaMatcher) {

    this.nombre = this.user.getUserName();
    this.imagen = this.user.getPhoto();
    this.mobileQuery = media.matchMedia('(max-width: 991px)');
    var vm = this;
    this._mobileQueryListener = () => {
      changeDetectorRef.detectChanges();
      vm.toggleNav(this.mobileQuery.matches);
      if (this.mobileQuery.matches) {
        vm.nav.toggle(false);
      }else{
        vm.nav.toggle(true);
      }
    };
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnDestroy() {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  ngOnInit() {
    //@ts-ignore
    //this.nav.toggle();
    this.navList = [{
      "cabecera": "Inventario",
      "activo": true,
      "opciones": [{
        "titulo": "Dashboard",
        "link": "app/inventario",
        "icono": "fa fa-meh-o",
        "activo": true
      },{
        "titulo": "Administracion",
        "link": "app/inventario/admin",
        "icono": "fa fa-meh-o",
        "activo": true
      },{
        "titulo": "Agregar",
        "link": "app/inventario/agregar",
        "icono": "fa fa-meh-o",
        "activo": true
      }]
    }, {
      "cabecera": "Configuracion",
      "activo": true,
      "opciones": [{
        "titulo": "config 1",
        "link": "app/home",
        "icono": "fa fa-meh-o",
        "activo": true
      }, {
        "titulo": "config 1",
        "link": "app/home",
        "icono": "fa fa-meh-o",
        "activo": true
      }]
    }, {
      "cabecera": "Usuarios",
      "activo": true,
      "opciones": [{
        "titulo": "Administracion",
        "link": "app/usuario/admin",
        "icono": "fa fa-meh-o",
        "activo": true
      }]
    }, {
      "cabecera": "Ventas",
      "activo": true,
      "opciones": [{
        "titulo": "Dashboard",
        "link": "app/ventas",
        "icono": "fa fa-meh-o",
        "activo": true
      },{
        "titulo": "Registrar",
        "link": "app/ventas/registrar",
        "icono": "fa fa-meh-o",
        "activo": true
      },{
        "titulo": "Historial",
        "link": "app/ventas/historial",
        "icono": "fa fa-meh-o",
        "activo": true
      }]
    }];

    if (this.page.includes('home')) {
      this.setActivo(1, true);
    }else{
      for (var i = 0; i < this.navList.length; ++i) {
        for (var e = 0; e < this.navList[i].opciones.length; ++e) {
          if (this.page.includes(this.navList[i].opciones[e].link)) {
            this.setActivo(i, false);
            this.setActiveOption(i,e);
            break;
          }
        }
      }
    }
    this.toggleNav(this.mobileQuery.matches);
  }

  setActivo(indice, clear) {    
    this.navList[indice];
    if (clear) {
      this.dashboard = false;
      for (var i = 0; i < this.navList.length; ++i) {
        this.navList[i].activo = true;
        for (var e = 0; e < this.navList[i].opciones.length; ++e) {
          this.navList[i].opciones[e].activo = true;
        }
      }
      this.toggleNav(this.mobileQuery.matches);
    } else {
      this.dashboard = true;
      for (var i = 0; i < this.navList.length; ++i) {
        if (i === indice) {
          this.navList[i].activo = false;
        } else {
          this.navList[i].activo = true;
        }
        for (var e = 0; e < this.navList[i].opciones.length; ++e) {
          this.navList[i].opciones[e].activo = true;
        }
      }
    }
  }

  setActiveOption(f, e) {
    this.toggleNav(this.mobileQuery.matches);
    this.dashboard = true;
    for (var i = 0; i < this.navList[f].opciones.length; ++i) {
      if (i === e) {
        this.navList[f].opciones[i].activo = false;
      } else {
        this.navList[f].opciones[i].activo = true;
      }
    }
  }

  public logOut() {
    this.afAuth.auth.signOut();
    window.location.href = "home";
  }

  private toggleNav(bool : boolean){
    if (bool) {
      this.nav.toggle(false);
    }else{
      this.nav.toggle(true);
    }
  }

}
