import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { ReplaySubject, Observable, Subject } from 'rxjs';
	
@Injectable()
export class UserInfoService {

	//private status: boolean = false;
	//private status = new ReplaySubject(1);
	private status = new Subject<boolean>();
	private userName : string;
	private photo : string;
	private unidad : string;
	constructor(private afAuth : AngularFireAuth) {
	}

	public setUserStatus(status: boolean) {
		this.status.next(status);
	}

	public getUserStatus(): Observable<boolean> {
		return this.status;
	}

	public setUserName(username : string){
		this.userName = username;
	}

	public getUserName() : string{
		return this.userName
	}

	public setPhoto(foto : string){
		this.photo = foto;
	}

	public getPhoto() : string{
		return this.photo;
	}

	public setUnidad(unidad : string){
		this.unidad = unidad;
	}

	public getUnidad() : string{
		return this.unidad;
	}
}
