import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { AngularFireStorage } from 'angularfire2/storage';
import { AngularFirestore } from 'angularfire2/firestore';
import { UserInfoService } from './../../../../provider/user-info/user-info.service';
import { InventarioService } from './../../../../provider/inventario/inventario.service';
import { RestrictionService } from './../../../../provider/restriction/restriction.service';
import swal from 'sweetalert2'

@Component({
	selector: 'app-p-inventario-add',
	templateUrl: './p-inventario-add.component.html',
	styleUrls: ['./p-inventario-add.component.css']
})
export class PInventarioAddComponent implements OnInit {
	public nombreFC = new FormControl('', [
		Validators.required,
	]);
	public barCodeFC = new FormControl('', [
		Validators.required,
	]);
	public stockFC = new FormControl('', [
		Validators.required,
	]);
	public listToAdd: Array<object> = [];
	private files: Array<any> = [];
	public articulosActuales : number;
	public isCargando : boolean = false;
	constructor(private storage: AngularFireStorage,
		private userI: UserInfoService,
		private db: AngularFirestore,
		private inv: InventarioService,
		private rsc : RestrictionService) {
	}

	ngOnInit() {
		swal({
		  title: 'Error!',
		  text: 'Do you want to continue',
		  type: 'error',
		  confirmButtonText: 'Cool'
		})
		this.inv.getSize().subscribe(rsp =>{
			this.articulosActuales = rsp;
			var maxArticles = this.rsc.getMaxArticles();
			if (this.articulosActuales < maxArticles && this.listToAdd.length === 0) {
				this.listToAdd = [{
					codigoBarra: "",
					nombre: "",
					tipoArticulo: "G",
					stock: 0,
					img: {
						href : "",
						ref : ""
					}
				}];
			}
		});
	}

	deleteFile(){
		console.log("delete");
	}
	
	validate(indice? : number) {
		if (this.listToAdd.length > 0) {
			this.isCargando = true;
			var cont = 0;
			if (indice!= undefined)cont = indice;
			var max = this.listToAdd.length;
			
			var obj = this.listToAdd[cont];

			var maxVariables = 0;
			var validated = 0;
			var asFile = false;
			for (let key in obj) {
				maxVariables++;
				if (obj[key].toString().length > 0) {
					validated++;
				}
				if (key.indexOf('img') >= 0) {
					if (obj[key]['href'].length > 0 ) {
						asFile = true;
					}
				}
			}
			if (maxVariables === validated && asFile) {
				//@ts-ignore
				obj.ingreso = Date.parse(new Date);
				//@ts-ignore
				obj.modificacion = Date.parse(new Date);		
				var file = this.files[cont].file;
				var fileRef = this.files[cont].fileRef;
				var ref = this.storage.ref(fileRef);
				//@ts-ignore
				obj.img.ref = fileRef;
				ref.put(file).then(rsp =>{
					ref.getDownloadURL().subscribe(href =>{
						obj["img"]["href"] = href;
						this.inv.agregarArticulo(obj, this.userI.getUnidad()).then(rsp =>{
							cont++;
							if (cont >= max) {
								this.listToAdd = [{
									codigoBarra: "",
									nombre: "",
									tipoArticulo: "G",
									stock: 0,
									img: {
										href : "",
										ref : ""
									}
								}];
								this.files = [];
								this.isCargando = false;
								return;
							}
							this.validate(cont);
							console.log("obj Insertado");
						}).catch(err =>{
							console.error("error al insertar");
						})
					})
				})
			}
		}		
	}

	uploadFile(event, i) {
		//@ts-ignore
		var date = Date.parse(new Date);
		var file = event.target.files[0];
		var fileRef = 'unidad/' + this.userI.getUnidad() + '/' + date + '-' + file.name;
		var reader = new FileReader(), rFilter = /^(image\/bmp|image\/cis-cod|image\/gif|image\/ief|image\/jpeg|image\/jpeg|image\/jpeg|image\/pipeg|image\/png|image\/svg\+xml|image\/tiff|image\/x-cmu-raster|image\/x-cmx|image\/x-icon|image\/x-portable-anymap|image\/x-portable-bitmap|image\/x-portable-graymap|image\/x-portable-pixmap|image\/x-rgb|image\/x-xbitmap|image\/x-xpixmap|image\/x-xwindowdump)$/i;
		reader.onload = (event: ProgressEvent) => {
			this.listToAdd[i]["img"]["href"] = (<FileReader>event.target).result;
		}

		reader.readAsDataURL(event.target.files[0]);
		this.files.push({
			"indice": i,
			"file": file,
			"fileRef": fileRef
		});
	}

	public addArticle() {
		if (this.articulosActuales < this.rsc.getMaxArticles() && (this.listToAdd.length + this.articulosActuales) < this.rsc.getMaxArticles()) {
			this.listToAdd.push({
				codigoBarra: "",
				nombre: "",
				tipoArticulo: "G",
				stock: 0,
				img: {
					href : "",
					ref : ""
				}
			});			
		}
		
	}

	public delArticle(i){
		this.listToAdd.splice(i,1);
		if (this.files.length > 0) {
			for (var e = 0; e < this.files.length; ++e) {
				if (this.files[e].indice === i) {
					this.files.splice(e,1);
				}
			}
		}
	}
}
