import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
	selector: 'app-p-usuario-mody',
	templateUrl: './p-usuario-mody.component.html',
	styleUrls: ['./p-usuario-mody.component.css']
})
export class PUsuarioModyComponent implements OnInit {
	@Input() objeto: Object = {};
	@Input() onEdit: Boolean;
	@Output() onClose: EventEmitter<any> = new EventEmitter();
	public objetoEditar: Object = {};
	private objetoAuxiliar: Object = {};
	public isReady: boolean = false;
	public nombre;

	constructor() {	}

	ngOnInit() {
		this.objetoAuxiliar = JSON.parse(JSON.stringify(this.objeto));
		this.objetoEditar = JSON.parse(JSON.stringify(this.objeto));
	}
	
	public close(modify) {
		var event = new CustomEvent("cat", {
			detail: {
				"object": this.objetoEditar,
				"mody": modify
			}
		});

		this.onClose.emit(event);
		this.onEdit = false;
	}

	verifyChange() {
		var cont = 0;
		var iguales = 0;
		for (let key in this.objetoEditar["obj"]) {
			cont++;
			if (this.objetoEditar["obj"][key] === this.objetoAuxiliar["obj"][key]) {
				iguales++;
			}
		}
		if (cont != iguales) {
			this.isReady = true;
		} else {
			this.isReady = false;
		}
	}
}
