import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { RecaptchaComponent } from 'ng-recaptcha';
import { AngularFirestore } from 'angularfire2/firestore';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
	selector: 'app-contacto',
	templateUrl: './contacto.component.html',
	styleUrls: ['./contacto.component.css']
})
export class ContactoComponent implements OnInit {

	private isCaptcha;
	public isEmail: boolean = false;
	public isNombre: boolean = false;
	public isContent: boolean = false;
	public isAsunto: boolean = false;
	public submited: boolean = false;
	public mensajeAlerta: string;
	public reactiveForm: FormGroup;
	public isAlert : boolean = false;
	@ViewChild('captchaRef') recaptcha: RecaptchaComponent;

	constructor(private db: AngularFirestore) { }

	ngOnInit() { }

	resolved(e: string) {
		this.isCaptcha = e;
		if (e != undefined || e != null) {
			this.submited = false;
			this.mensajeAlerta = 'reCaptcha validado.';
			this.isAlert = false;
		}
	}

	public captchaReset() {
		//@ts-ignore
		//this.recaptcha.execute();
		this.recaptcha.reset();
	}

	public sendEmail(f: NgForm) {
		
		var objeto = {
			email : f.value.email,
			html: f.value.contenido,
			asunto: f.value.asunto,
			nombre: f.value.nombre
		};

	    if (this.isCaptcha != null && this.isCaptcha != undefined) {
	      f.reset();
	      this.mensajeAlerta = 'Mensaje enviado correctamente.';
	      this.db.collection('messages').add(objeto);
	      this.captchaReset();
	      this.isAlert = true;
	      this.submited = false;
	    }else{
	      this.mensajeAlerta = 'Debe validar el Captcha antes de enviar el mensaje.';
	      this.isAlert = true;
	      this.submited = true;
	    }
	}

	public validateFields(f: NgForm) {

		var email = this.validateEmail(f.value.email);
		if (email) {
			this.isEmail = false;
		} else {
			this.isEmail = true;
		}

		if (f.value.nombre.length > 0) {
			this.isNombre = false;
		} else {
			this.isNombre = true;
		}

		if (f.value.contenido.length > 0) {
			this.isContent = false;
		} else {
			this.isContent = true;
		}

		if (f.value.asunto.length > 0) {
			this.isAsunto = false;
		} else {
			this.isAsunto = true;
		}

		if (!this.isEmail && !this.isContent && !this.isAsunto) {
			this.sendEmail(f);
		}
	}

	private validateEmail(email) {
		var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(email);
	}

	getLength(e) {
		var largo = 0;
		if (e) largo = e.length;
		return largo;
	}
}
