import { BrowserModule } from '@angular/platform-browser';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { RecaptchaModule, RecaptchaLoaderService, RecaptchaComponent } from 'ng-recaptcha';
import { AppRoutingModule } from './app.routing';

import { AppComponent } from './app.component';
import { CarouselComponent } from './carousel/carousel.component';
import { NavComponent } from './nav/nav.component';
import { InfotopComponent } from './infotop/infotop.component';
import { PortafolioComponent } from './portafolio/portafolio.component';
import { ContactoComponent } from './contacto/contacto.component';
import { FooterComponent } from './footer/footer.component';


import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireStorageModule } from 'angularfire2/storage';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { environment } from './../environments/environment';
import { FormsModule , ReactiveFormsModule} from '@angular/forms';
import { LoginPageComponent } from './login-page/login-page.component';
import { HomeComponent } from './home/home.component';

/* Provider */
import { UserInfoService } from './../provider/user-info/user-info.service';
import { InventarioService } from './../provider/inventario/inventario.service';
import { AuthGuardService } from './../security/auth/auth-guard.service';
import { AuthService } from './../security/auth/auth.service';
import { RestrictionService } from './../provider/restriction/restriction.service';
import { IndicadoresService } from './../provider/indicadores/indicadores.service';
import { UsuariosService } from './../provider/usuarios/usuarios.service';

/* Private Access */
import { PHomeComponent } from './private-access/p-home/p-home.component';
import { PNavComponent } from './private-access/p-nav/p-nav.component';
import { IndicadoresComponent } from './private-access/modules/indicadores/indicadores.component';
import { PInventarioDashboardComponent } from './private-access/inventario/p-inventario-dashboard/p-inventario-dashboard.component';
import { PInventarioComponent } from './private-access/inventario/p-inventario/p-inventario.component';
import { PInventarioAddComponent } from './private-access/inventario/p-inventario-add/p-inventario-add.component';
import { PInventarioModyComponent } from './private-access/inventario/p-inventario-mody/p-inventario-mody.component';
import { PUsuarioComponent } from './private-access/usuario/p-usuario/p-usuario.component';
import { PUsuarioDashboardComponent } from './private-access/usuario/p-usuario-dashboard/p-usuario-dashboard.component';
import { PUsuarioModyComponent } from './private-access/usuario/p-usuario-mody/p-usuario-mody.component';
import { PVentasDashboardComponent } from './private-access/ventas/p-ventas-dashboard/p-ventas-dashboard.component';
import { PVentasRegistrarComponent } from './private-access/ventas/p-ventas-registrar/p-ventas-registrar.component';
import { PVentasHistorialComponent } from './private-access/ventas/p-ventas-historial/p-ventas-historial.component';

/* Material */
import {
  MatAutocompleteModule,
  MatButtonModule,
  MatButtonToggleModule,
  MatCardModule,
  MatCheckboxModule,
  MatChipsModule,
  MatDatepickerModule,
  MatDialogModule,
  MatDividerModule,
  MatExpansionModule,
  MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule,
  MatNativeDateModule,
  MatPaginatorModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatRadioModule,
  MatRippleModule,
  MatSelectModule,
  MatSidenavModule,
  MatSliderModule,
  MatSlideToggleModule,
  MatSnackBarModule,
  MatSortModule,
  MatStepperModule,
  MatTableModule,
  MatTabsModule,
  MatToolbarModule,
  MatTooltipModule,
  MatSidenav
} from '@angular/material';

import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { FirebaseDateparsePipe } from './../pipe/firebase/firebase-dateparse.pipe';
import { UsyncInputDirective } from './../directive/input/usync-input.directive';
import { PSpinnerComponent } from './p-spinner/p-spinner.component';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { NotFoundComponent } from './not-found/not-found.component';
import { ToasterModule, ToasterService, ToasterContainerComponent} from 'angular2-toaster';

@NgModule({
  declarations: [
    AppComponent,
    CarouselComponent,
    NavComponent,
    InfotopComponent,
    PortafolioComponent,
    ContactoComponent,
    FooterComponent,
    LoginPageComponent,
    HomeComponent,
    PHomeComponent,
    PNavComponent,
    IndicadoresComponent,
    PInventarioComponent,
    FirebaseDateparsePipe,
    PInventarioAddComponent,
    UsyncInputDirective,
    PInventarioModyComponent,
    PSpinnerComponent,
    PUsuarioComponent,
    PUsuarioModyComponent,
    PVentasDashboardComponent,
    PVentasRegistrarComponent,
    PVentasHistorialComponent,
    PInventarioDashboardComponent,
    PUsuarioDashboardComponent,
    NotFoundComponent
  ],
  imports: [
    BrowserModule,
    RecaptchaModule.forRoot(),
    AngularFireModule.initializeApp(environment.firebase, 'ubeside'), // imports firebase/app needed for everything
    AngularFirestoreModule, // imports firebase/firestore, only needed for database features
    AngularFireAuthModule, // imports firebase/auth, only needed for auth features,
    AngularFireStorageModule, // imports firebase/storage only needed for storage features
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatDividerModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSnackBarModule,
    MatSortModule,
    MatStepperModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
    BrowserAnimationsModule,
    SweetAlert2Module.forRoot(),
    ToasterModule
  ],
  providers: [
    RecaptchaLoaderService,
    RecaptchaComponent,
    UserInfoService,
    AuthGuardService,
    AuthService,
    InventarioService,
    IndicadoresService,
    UsuariosService,
    ToasterService
  ],
  bootstrap: [AppComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule { }
