import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore'
import { ReplaySubject, Observable } from 'rxjs'
import { UserInfoService } from './../user-info/user-info.service';
import { AngularFireStorage } from 'angularfire2/storage';

@Injectable({
	providedIn: 'root'
})
export class IndicadoresService {

	private lastMody = new ReplaySubject(1);

	constructor(private db: AngularFirestore,
		private user: UserInfoService,
		private storage: AngularFireStorage) {
		//setup/test/indicadores/lastUpdate
			this.db.doc('/setup/'+this.user.getUnidad()+'/indicadores/lastUpdate').valueChanges().subscribe(rsp=>{
				this.lastMody.next(rsp);
			})
		}


	public getLastModify() : ReplaySubject<any>{
		return this.lastMody;
	}
}
