import { Component } from '@angular/core';
import { UserInfoService } from './../provider/user-info/user-info.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router, NavigationEnd } from '@angular/router';
import { AngularFirestore } from 'angularfire2/firestore';
import { InventarioService } from './../provider/inventario/inventario.service';
import { RestrictionService } from './../provider/restriction/restriction.service';

@Component({
	selector: 'app-root',
	templateUrl: './app.component.html',
	styleUrls: ['./app.component.css']
})
export class AppComponent {
	public isLogged : boolean = false;
	public page : string;
	constructor(private user: UserInfoService,
    private afAuth: AngularFireAuth,
    private router : Router,
    private db : AngularFirestore,
    private inventario : InventarioService,
    private rsc : RestrictionService) {

		this.router.events.subscribe(event =>{
			if (event instanceof NavigationEnd) {
				if (event.url.includes('app')) {					
					this.isLogged = true;
					this.page = event.url;
				}else{
					this.isLogged = false;
				}
			}
		})

		var vm = this;
		this.afAuth.auth.onAuthStateChanged(function(user) {
			if (user) {
				//vm.afAuth.auth.currentUser.updateProfile({displayName: user.displayName, photoURL: 'test'})
				vm.db.doc('/setup/'+user.photoURL).valueChanges().subscribe(rsp=> {
					console.log(rsp);
					vm.rsc.setMaxArticles(rsp['maxinventory']);
					//vm.user.setUnidad(rsp['unidad']);
					//vm.cargando = false;
					vm.user.setUserName(user.displayName);
					vm.user.setUnidad(user.photoURL);
					vm.inventario.setUnidad(user.photoURL);
					vm.user.setUserStatus(true);
				});

				// User is signed in.				
				//var email = user.email;
				//var emailVerified = user.emailVerified;
				//var isAnonymous = user.isAnonymous;
				//var uid = user.uid;
				//var providerData = user.providerData;
			} else {
				vm.user.setUserStatus(false);
				//vm.router.navigate(['login']);
				console.log("firebaseout");
			}
		});
	}
}
