import { Component, OnInit, ViewChild } from '@angular/core';
import { UsuariosService } from './../../../../provider/usuarios/usuarios.service';
import { PUsuarioModyComponent } from './../p-usuario-mody/p-usuario-mody.component';

@Component({
	selector: 'app-p-usuario',
	templateUrl: './p-usuario.component.html',
	styleUrls: ['./p-usuario.component.css']
})
export class PUsuarioComponent implements OnInit {

	public displayedColumns: string[] = ['imagen', 'nombre', 'apellido', 'identificador', 'nacimiento', 'ingreso', 'acciones'];
	public dataSource: Array<any> = [];
	public isCargando : boolean = false;
	@ViewChild(PUsuarioModyComponent) mody: PUsuarioModyComponent;
	public selectedItem: any;
	public isOnEdit: boolean = false;

	constructor(private usuarios: UsuariosService) { }

	ngOnInit() {
		this.isCargando = true;
		this.usuarios.getUsuarios().subscribe(rsp=>{
			this.dataSource = [];
			this.dataSource = rsp;
			this.isCargando = false;
		});
	}

	public dateParse(date): string {
		var rsp = "";
		if (date) {
			var dd = new Date(date);
			var split = dd.toJSON().split('T');
			split = split[0].split('-');
			rsp = split[2] + '-' + split[1] + '-' + split[0];
		}
		return rsp;
	}

	public onEdit(item) {
		this.selectedItem = Object.assign({}, item);
		this.isOnEdit = true;
	}

	public onClose(event : Event){
		this.isOnEdit = false;
		//@ts-ignore
		if (event.detail.mody) {
			//@ts-ignore
			this.usuarios.actualizar(event.detail.object);
		}else{

		}
	}

}
