import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';
import { Chart } from 'chart.js';

@Component({
	selector: 'app-indicadores',
	templateUrl: './indicadores.component.html',
	styleUrls: ['./indicadores.component.css']
})
export class IndicadoresComponent implements OnInit {
	@ViewChild('canvas') canvas: ElementRef;
	@Input() data : Object;
	@Input() options : Object;
	@Input() porcentaje;
	public chart: any;
	public pvalor : string;
	public valor : string;
	constructor() { }

	ngOnInit() {
		window.addEventListener("resize", function(){
			
		});
		Chart.defaults.global.legend.display = true;
		Chart.defaults.global.tooltips.enabled = true;
		
		if (this.porcentaje) {

			this.valor = ((parseInt(this.data[0]) * 100) / this.data[1]).toString();
			this.pvalor = 'p'+this.valor;
			var vm = this;
			setInterval(function(){
				vm.valor = ((parseInt(vm.data[0]) * 100) / vm.data[1]).toString();
				vm.pvalor = 'p'+vm.valor;
			},1000);

		}else{
			var ctx = this.canvas.nativeElement;

			this.chart = new Chart(ctx, {
				type: 'pie',
				data: this.data,
				options: this.options
			});
		}
	}
	/*
		stock actual * 100% / maximostock
	*/

}
