import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { RecaptchaComponent } from 'ng-recaptcha';
import { AngularFirestore } from 'angularfire2/firestore';
import { NgForm, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AngularFireAuth } from 'angularfire2/auth';
import { UserInfoService } from './../../provider/user-info/user-info.service';
import { InventarioService } from './../../provider/inventario/inventario.service';

@Component({
	selector: 'app-login-page',
	templateUrl: './login-page.component.html',
	styleUrls: ['./login-page.component.css']
})
export class LoginPageComponent implements OnInit {
	private isCaptcha;
	public isEmail: boolean = false;
	public isPassword: boolean = false;
	public mensajeAlerta: string;
	public reactiveForm: FormGroup;
	public isAlert: boolean = false;
	public isCargando : boolean = false;

	constructor(public router: Router,
		private afAuth: AngularFireAuth,
		private userInfo: UserInfoService,
		private inventario : InventarioService,
		private db : AngularFirestore) {
		this.userInfo.getUserStatus().subscribe(rsp=>{
			this.isCargando = true;
			if (rsp) {
				window.location.href = 'app/home';
				this.isCargando = false;
			}else{
				this.isCargando = false;
			}
		});
	}

	ngOnInit() {
	}

	private login(email, password) {
		var vm = this;
		this.isCargando = true;
		this.afAuth.auth.signInWithEmailAndPassword(email, password).then(function(response) {
			vm.userInfo.setUserStatus(true);
			window.location.href = 'app/home';
			console.log("Login");
			this.isCargando = false;
		}).catch(function(error) {
			//vm.cargando = false;
			console.log("error");
			this.isCargando = false;
		})
	}

	public validateFields(f: NgForm) {

		var email = this.validateEmail(f.value.email);
		if (email) {
			this.isEmail = false;
		} else {
			this.isEmail = true;
		}

		if (f.value.password.length > 0) {
			this.isPassword = false;
		} else {
			this.isPassword = true;
		}

		if (!this.isEmail && !this.isPassword) {
			this.login(f.value.email, f.value.password);
		}
	}

	private validateEmail(email) {
		var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return re.test(email);
	}
}
