import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
	selector: 'app-p-inventario-mody',
	templateUrl: './p-inventario-mody.component.html',
	styleUrls: ['./p-inventario-mody.component.css']
})
export class PInventarioModyComponent implements OnInit {

	@Input() objeto: Object = {};
	@Input() onEdit : boolean;
	@Output() onClose : EventEmitter<any> = new EventEmitter();
	public objetoEditar : Object = {};
	private objetoAuxiliar : Object = {};
	public isReady : boolean = false;
	public nombre;
	constructor() { }

	ngOnInit() {
		this.objetoAuxiliar = JSON.parse(JSON.stringify(this.objeto));
		this.objetoEditar = JSON.parse(JSON.stringify(this.objeto));
	}

  public close(modify){

  	//@ts-ignore
  	this.objetoEditar["obj"]["modificacion"] = Date.parse(new Date());

  	var event = new CustomEvent("cat", {
	  detail: {
	    "object": this.objetoEditar,
	    "mody" : modify
	  }
	});

  	this.onClose.emit(event);
  	this.onEdit = false;
  }

  verifyChange(){
  	var cont = 0;
  	var iguales = 0;  	
  	for (let key in this.objetoEditar["obj"]) {
  		cont++;
  		if (this.objetoEditar["obj"][key] === this.objetoAuxiliar["obj"][key]) {
  			iguales++;
  		}
  	}
  	if (cont != iguales) {
  		this.isReady = true;
  	}else{
		this.isReady = false;
  	}
  }
}
