import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore'
import { ReplaySubject , Observable} from 'rxjs'
import { UserInfoService } from './../user-info/user-info.service';
import { AngularFireStorage } from 'angularfire2/storage';

@Injectable({
  providedIn: 'root'
})
export class InventarioService {

  //private inventario : AngularFirestoreCollection<any>;
	private inventario = new ReplaySubject(1);
  private cantidad  = new ReplaySubject(1);
  private cantidadUns : number;
  constructor(private db : AngularFirestore,
              private user : UserInfoService,
              private storage : AngularFireStorage) {
    this.cantidad.next(0);
  }

  public setUnidad(unidad? : string){
  	//var url = "/inventario/unidad/"+unidad;
    var url = "/inventario/unidad/"+this.user.getUnidad();
    var vm = this;    

    //afs.collection('items', ref => ref.where('size', '==', 'large'))
    this.db.collection('/inventario', ref => ref.where('unidad', '==', this.user.getUnidad())).snapshotChanges().subscribe(rsp=>{
    //this.db.collection(url).snapshotChanges().subscribe(rsp=>{
      var listaAux = [];
      if (rsp.length > 0) {
        rsp.forEach(function(doc, index, array) {
          var data = doc.payload.doc.data();
          var obj = {
            "obj" : doc.payload.doc.data(),
            "id" : doc.payload.doc.id
          }
          listaAux.push(obj)
          if ((index+1) >= array.length) {
            console.log("articulos: "+array.length);
            vm.cantidadUns = array.length;
            vm.cantidad.next(array.length);
            vm.inventario.next(listaAux);
          }
        })
      }else{
        vm.cantidad.next(0);
        vm.inventario.next(listaAux);
      }
    })
  }

  public getInventario() : ReplaySubject<any>{
  	return this.inventario;
  }

  public getSize() : ReplaySubject<any>{
    return this.cantidad;
  }

  public agregarArticulo(obj, unidad): Promise<object>{
    //var url = "/inventario/unidad/"+unidad;
    obj['unidad'] = this.user.getUnidad();
    return this.db.collection('/inventario').add(obj);
  }

  public eliminarArticulo(obj) : Observable<any>{
    var url = "/inventario/"+obj.id;
    var vm = this;
    this.db.doc(url).delete();
    return this.storage.ref(obj.obj.img.ref).delete();
  }

  public getSizeUns() : number{
    return this.cantidadUns;
  }

  public actualizarArticulo(obj) : Promise<any>{
    var url = "/inventario/"+obj.id;
    var vm = this;
    return this.db.doc(url).update(obj.obj);
  }

}
